.social-links {
	display: flex;
	justify-content: center;
	margin: 0 0 2rem 0;
	padding: 0;
	list-style: none;
}
.social-links li:not(:last-child) {
	margin-right: 3.5rem;
}
.social-links li a {
	display: flex;
	opacity: 0.9;
}
.social-links li a:hover {
	opacity: 1;
	transform: scale(1.2);
}
.social-links .icon-holder {
	display: flex;
	width: 4rem;
}
.social-links .icon-holder img {
	width: 100%;
	height: auto;
}